import _ from 'lodash';
import { ref, watch } from 'vue';
import { InventoryItem } from '../itemConstants';
import { NpcData, OutfitData } from '../types';

//  MVU 
const defaultMvuData = {
  PlayerData: {
    character: {
      name: '',
      level: 1,
      gender: '',
      race: '',
      height: '',
      build: '',
      appearance: '',
      personality: '',
    },
    status: {
      health: {
        current: 12,
        max: 12,
      },
      mood: {
        current: 0,
        max: 100,
      },
      arousal: {
        current: 50,
        max: 100,
      },
    },
    attributes: {
      shooting: 0,
      melee: 0,
      construction: 0,
      mining: 0,
      cooking: 0,
      planting: 0,
      animals: 0,
      crafting: 0,
      artistic: 0,
      medical: 0,
      social: 0,
      intellectual: 0,
    },
    progress: {
      questPhase: '',
      partyExperience: {
        current: 0,
        max: 100,
      },
      partyAttrPoints: 0,
      currentQuest: {},
      nextQuest: {},
      pendingQuest: {},
      completedQuest: {},
    },
    settings: {
      date: '',
      time: '',
      weather: '',
      currentRegion: '',
      currentLocation: '',
      nearbyNPC: {} as Record<string, NpcData>,
      factionPrestige: {},
      bestiary: {},
      anecdotes: {},
    },
  },
  PlayerDynamicData: {
    inventory: {} as Record<string, InventoryItem>,
    equipment: {
      leftHand: 'none',
      rightHand: 'none',
      body: 'none',
    },
    clothingAttributes: {
      defense: 0,
      comfort: 0,
      warmth: 0,
      social: 0,
      weight: 0,
    },
    gold: 100,
  },
  FollowerNPCData: {},
  Wardrobe: {
    ownedOutfits: {} as Record<string, OutfitData>,
    currentOutfit: 'none',
    //todo outfit
  },
  ArchivedData: {
    factionPrestige: {},
    bestiary: {},
    anecdotes: {},
  },
  System: {
    mainStoryMode: true,
  },
};

const ERA_EVENT = {
  insertByObject: 'era:insertByObject',
  updateByObject: 'era:updateByObject',
  insertByPath: 'era:insertByPath',
  updateByPath: 'era:updateByPath',
  deleteByObject: 'era:deleteByObject',
  deleteByPath: 'era:deleteByPath',
};
interface Command {
  event: string;
  detail: any;
}

const rawMvuData = ref<any | null>(null);
const currentMessageId = ref(0);
const mvu = ref(_.cloneDeep(defaultMvuData));

export function useMvuData() {
  /**
   *  MVU 
   * @param messageId  ID
   */
  const refreshMvuData = async (messageId?: number | 'latest') => {
    try {
      eventEmit('era:requestWriteDone');
    } catch (error) {
      console.error(` ${messageId}  MVU :`, error);
      rawMvuData.value = null;
    }
  };

  // MVU
  const handleMvuUpdate = async (commands: Command[], successCallback?: () => void) => {
    if (!rawMvuData.value) {
      console.warn(' MVU , ');
      toastr.error(': ');
      return;
    }
    try {
      commands.forEach((command: Command) => {
        const event = command.event;
        if (event in ERA_EVENT) {
          //  detail 
          eventEmit(ERA_EVENT[event as keyof typeof ERA_EVENT], command.detail);
        } else {
          console.warn(`Invalid ERA event received: ${event}`);
        }
      });

      // await refreshMvuData('latest');
      successCallback?.();
    } catch (error) {
      console.error(' MVU :', error);
      toastr.error(': ');
    }
  };

  //  MVU ,  UI
  watch(rawMvuData, newData => {
    if (!newData) {
      mvu.value = _.cloneDeep(defaultMvuData);
      console.warn('MVU , ');
      return;
    }

    // : 
    const baseState = _.cloneDeep(defaultMvuData);
    mvu.value = _.merge(baseState, newData);
  });

  const initialize = async () => {
    try {
      currentMessageId.value = getCurrentMessageId();
      refreshMvuData();
    } catch (error) {
      console.error(':', error);
    }
  };

  eventOn('era:writeDone', detail => {
    //  API 
    // if (detail.actions.apiWrite) {
    //   console.log(' API UI ');
    //   // return;
    // }

    if (!detail || !detail.statWithoutMeta) return;

    //  statWithoutMeta  UI ERA 
    rawMvuData.value = detail.statWithoutMeta;

    if (mvu.value.System.mainStoryMode) {
      // 
      const quest = rawMvuData.value?.PlayerData?.progress?.currentQuest || {};
      // const currentQuests:string[] = Object.values(quest)
      //   .filter((item:any) => item.name)
      //   .map((item:any) => item.name);
      const currentQuests: string[] = Object.keys(quest);

      updateWorldbookWith('', worldbook => {
        const QUEST_KEYWORDS = ['', ''];
        const STRATEGY_TYPE = {
          CONSTANT: 'constant' as const,
          SELECTIVE: 'selective' as const,
        };

        return worldbook.map(entry => {
          const isQuestEntry = QUEST_KEYWORDS.some(keyword => entry.name.includes(keyword));

          //  strategy 
          if (!isQuestEntry || !entry.strategy) {
            return entry;
          }

          // 
          const isQuestActive = currentQuests.some(quest => entry.strategy.keys.includes(quest));
          const newStrategyType = isQuestActive ? STRATEGY_TYPE.CONSTANT : STRATEGY_TYPE.SELECTIVE;

          // 
          if (entry.strategy.type === newStrategyType) {
            return entry;
          }

          //  strategy
          return {
            ...entry,
            strategy: {
              ...entry.strategy, //  strategy 
              type: newStrategyType, //  type 
            },
          };
        });
      });
    }
  });

  return {
    mvu,
    rawMvuData,
    currentMessageId,
    refreshMvuData,
    handleMvuUpdate,
    initialize,
  };
}
